// @flow

import getBrowserSize from './getBrowserSize';
import { isBrowser, isNode } from './env';

function getCommonInfo() {
  if (isBrowser) return getBrowserInfo();
  if (isNode) return getNodeInfo();
  return null;
}

function getBrowserInfo() {
  const info = {};
  const [w, h] = getBrowserSize();
  if (w && h) info.window = `${w}x${h}`;
  if (navigator.userAgent) info.userAgent = navigator.userAgent;
  if (location.href) info.href = location.href;
  return info;
}

function getNodeInfo() {
  return {
    version: process.versions.v8,
  };
}

export default getCommonInfo;



