// @flow

import upperFirst from 'lodash.upperfirst';
import unhandledError from './utils/unhandled.error';
import unhandledPromise from './utils/unhandled.promise';
import getCommonInfo from './utils/getCommonInfo';
import UserWatcher from './utils/UserWatcher';

let inited = false;
let userWatcher = null;
const callbacks = [];

function onError(fn: Function): void {
  if (!inited) {
    unhandledError(onUnhandledError);
    unhandledPromise(onUnhandledError);
    userWatcher = new UserWatcher();
    inited = true;
  }

  callbacks.push(fn);
}

function onUnhandledError(
  error?: Object,
  errMessage?: string,
  errFile?: string,
  errLine?: string,
  errCol?: string,
) {
  const info = {};

  if (error && error.message) info.message = error.message;
  else if (error && error.type) info.message = upperFirst(error.type);

  if (error && error.filename)
    info.filename = `${error.filename}:${error.lineno}:${error.colno}`;
  else if (errFile)
    info.filename = `${errFile}:${errLine || '?'}:${errCol || '?'}`;

  if (error && error.reason) info.reason = error.reason;
  if (error && error.stack) info.stack = error.stack;

  returnInfo(info);
}

function returnInfo(errorInfo: Object): void {
  const actions = userWatcher ? userWatcher.toString() : '';
  const actionsObj = actions ? { actions } : {};
  const commonInfo = getCommonInfo();
  const allInfo = Object.assign(commonInfo, errorInfo, actionsObj);
  callbacks.forEach(callback => callback(allInfo));
}

export default onError;



