import { isBrowser } from './env';

export default fn => {
  if (isBrowser) {
    const prevHandler = window.onerror;

    window.onerror = (message, filename, lineno, colno, error) => {
      fn(error, message, filename, lineno, colno);
      console.info('# prevHandler', prevHandler);
      if (prevHandler) prevHandler(message, filename, lineno, colno, error);
    };
  } else {
    process.on('uncaughtException', fn);
  }
};



